import {
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  Input
} from '@angular/core';

@Component({
  // tslint:disable-next-line:component-selector
  selector: '[sofButton]',
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <div
      class="button-wrapper d-flex justify-content-center align-items-center position-relative"
    >
      <sof-loading
        *ngIf="loading"
        size="sm"
        class="position-absolute"
      ></sof-loading>
      <sof-svg-icon
        *ngIf="icon"
        [icon]="icon"
        [size]="iconSize"
        [class.mr-2]="contentWrapper.childNodes.length !== 0 || iconSuffix"
        [class.invisible]="loading"
      >
      </sof-svg-icon>
      <span #contentWrapper [class.invisible]="loading">
        <ng-content></ng-content>
      </span>
      <sof-svg-icon
        *ngIf="iconSuffix"
        [icon]="iconSuffix"
        [size]="iconSuffixSize"
        [class.ml-2]="contentWrapper.childNodes.length !== 0"
        [class.invisible]="loading"
      >
      </sof-svg-icon>
    </div>
  `,
  styleUrls: ['./button.directive.scss']
})
export class ButtonDirectiveComponent {
  /**
   * Whether or not the button is in loading state.
   * Content of the button will be hidden and a spinner will show.
   */
  @Input() loading: boolean;

  /**
   * Defines which icon must be shown next to the button label.
   * The icon will be positioned in front of the label.
   */
  @Input() icon: string;

  /**
   * Defines which icon must be shown next to the button label.
   * The icon will be positioned after the label.
   */
  @Input() iconSuffix: string;

  /**
   * Defines what size the icon will have.
   * The default size will be 16px.
   */
  @Input() iconSize: '8' | '12' | '16' | '20' | '24' | '28' | '32' | '48' =
    '16';

  /**
   * Defines what size the suffix icon will have.
   * The default size will be 16px.
   */
  @Input() iconSuffixSize:
    | '8'
    | '12'
    | '16'
    | '20'
    | '24'
    | '28'
    | '32'
    | '48' = '16';

  /**
   * Defines what the button type must be. Defaults to: 'button'.
   * Possible values are based on the HTML standard.
   * https://developer.mozilla.org/en-US/docs/Web/HTML/Element/button
   */
  @HostBinding('attr.type')
  @Input()
  type: 'button' | 'reset' | 'submit' = 'button';
}
